// 发布订阅系统
class EventCenter {
  // 缓存数据和绑定函数
  eventList = new Map();
  /**
   * 绑定监听函数
   * @param name 事件名称
   * @param f 绑定函数
   */
  on(name, f) {
    let eventInfo = this.eventList.get(name);
    // 如果没有缓存，则初始化
    if (!eventInfo) {
      eventInfo = {
        data: {},
        callbacks: new Set()
      };
      // 放入缓存
      this.eventList.set(name, eventInfo);
    }

    // 记录绑定函数
    eventInfo.callbacks.add(f);
  }

  // 解除绑定
  off(name, f) {
    const eventInfo = this.eventList.get(name);
    // eventInfo存在且f为函数则卸载指定函数,否则卸载所有函数
    if (eventInfo) {
      if (typeof f === 'function') {
        eventInfo.callbacks.delete(f);
      } else {
        eventInfo.callbacks = new Set();
      }
    }
  }

  // 发送数据
  dispatch(name, data) {
    const eventInfo = this.eventList.get(name);
    // 当数据不相等时才更新
    if (eventInfo && eventInfo.data !== data) {
      eventInfo.data = data;
      // 遍历执行所有绑定函数
      for (const f of eventInfo.callbacks) {
        f(data);
      }
    }
  }
}

// 实例化发布订阅对象
const eventCenter = new EventCenter();

// 应用的数据通信方法集合
export class EventCenterForApp {
  constructor(appId) {
    this.appId = appId;
  }
  /**
   * 向指定应用发送数据
   * @param appIds 应用id,可为多个,单个唯一不可以重复（父应用往多个应用发送数据）
   * @param data 对象数据
   */
  dispatch(appIds, data) {
    if (Array.isArray(appIds)) {
      appIds.map(appId => {
        eventCenter.dispatch(appId, data);
      });
    }
  }

  /**
   * 监听基座应用发送的数据
   * @param cb 绑定函数
   */
  addDataListener(cb) {
    eventCenter.on(this.appId, cb);
  }

  /**
   * 清空某个应用的监听函数
   * @param appId 子应用名称
   */
  clearDataListener() {
    eventCenter.off(this.appId);
  }
}
