import { log } from './index';
const appsMap = new Map();

export const setApp = (id, instance) => {
  if (appsMap.has(id)) {
    log.error(`App ${id} 已存在, 重复实例化失败.`);
    return false;
  }
  appsMap.set(id, instance);
  return true;
};

export const getApp = id => appsMap.get(id);
export const getApps = () => Array.from(appsMap.values);
export const removeApp = id => appsMap.delete(id);
